.categories {
  &__wrapper {
    display: grid;
    row-gap: 40px;
  }

  @include onTablet {
    row-gap: 64px;
  }
}

.category {

  &__photos {
    @include pageGrid;
    margin-bottom: 16px;
  }

  &__link {
    overflow: hidden;

    position: relative;
    &--wide {
      @include onTablet {
        grid-column: span 4;
      }
      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;
      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }

  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform $transition-speed;

    &:hover {
      transform: scale(1.1);
      z-index: 1;
    }
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
  }
}
